* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: rgb(31, 88, 40);
  font-family: "Open Sans", sans-serif;
  min-height: 100vh;
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 1.5rem auto;
  width: 80vw;
  min-height: 100vh;
}

.accordion {
  background-color: rgba(215, 253, 221, 0.925);
  border-width: transparent;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 1.5rem;
  width: 70%;
}

.faq-heading {
  font-size: 30px;
  margin-bottom: 1.5rem;
  text-align: center;
}

.faq-body {
  width: 90%;
}

.question-heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0.7rem;
}

.question-heading h4 {
  flex-grow: 1;
}

.question-heading button {
  background-color: rgb(58, 134, 69);
  border-radius: 50%;
  border: 1px solid transparent;
  color: rgba(215, 253, 221, 0.925);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 17px;
  height: 25px;
  padding: 2px 4px;
  width: 25px;
  margin-left: 2rem;
}

.question-heading button:hover {
  opacity: 0.7;
}

.question-heading button:focus {
  border: none;
  outline: none;
}

.each-faq {
  background-color: rgb(233, 252, 236);
  box-shadow: 4px 4px 12px rgba(0, 0, 0, 0.5);
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1rem;
}

.each-faq p {
  color: #444;
  font-size: 14px;
  letter-spacing: 1px;
  padding: 3px 0;
}

@media screen and (max-width: 790px) {
  .wrapper {
    width: 90vw;
  }

  .accordion {
    width: 90%;
  }

  .faq-heading {
    font-size: 22px;
  }

  .question-heading h4 {
    font-size: 15px;
  }
}

@media screen and (max-width: 480px) {
  .accordion,
  .faq-body {
    width: 100%;
  }
}
